
#btns{
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    
   background-color: rgb(240,240,240);
   
    /* padding: 10px; */
    border-radius: 20px;
}

#bns{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
 justify-content: space-evenly;
  padding:20px;
}

button{
    background-color: rgb(215,215,215);
    color: rgb(76,76,76);
    font-weight: 600;
    outline: none;
    border:solid rgb(224,224,224) 1px;
    width: max-content;
    height: max-content;
    border-radius: 10px;
    font-size: large;
    padding:10px;
   
}
h1{
    padding: 5px 40px;
}

button:hover{
    background-color: rgba(215, 215, 215, 0.181);
    color: rgb(76,76,76);
    font-weight: 600;
    outline: none;
    border:solid rgb(179,179,179) 1px;
    width: max-content;
    height: max-content;
    border-radius: 10px;
    font-size: large;
    padding:10px;
    cursor: pointer;
}
