
#ts{
    border:solid rgb(224,224,224) 1px;
    text-align: left;
    width:100%;
    border-collapse: collapse;
}

#ts>thead{
    background-color:  rgb(240,240,240);
  
    
}

#ts>thead>tr>th{
    height: 10px;
    padding: 5px;
    border: 1px solid rgb(224,224,224);
}

#ts>tbody>tr{
cursor: pointer;
}

#ts>tbody>tr:hover{
    cursor: pointer;
    background-color:  rgb(240,240,240);
    }
#ts>tbody>tr>td{
  
    border: 1px solid rgb(224,224,224);
    height: 10px;
    padding: 5px;
}